	module.exports = {"pathContext":{"posts":[{"html":"<h1>My Affinity Method</h1>\n<p>When performing research via surveys, it can be tricky to categorize free text responses.</p>\n<p>One of the most useful techniques I've developed follows the general proceedure of <a href=\"http://asq.org/learn-about-quality/idea-creation-tools/overview/affinity.html\">creating an Affinity Diagram</a>.</p>\n<h3>Three Rules</h3>\n<ul>\n<li><strong>Do NOT make category assumptions</strong>. It doesn't matter if you <em>know</em> what the themes will be.</li>\n<li><strong>Record an ID to get back to a response</strong>. It saves a lot of time and headaches to record a unique ID (or email address or something) so you can find a person later for following up.</li>\n<li><strong>Do NOT group your groups until you're done processing</strong>. It doesn't take that long to do, which is all the more reason to just wait until you've finished going through the raw data.</li>\n</ul>\n<h3>The Process</h3>\n<p><em>This process assumes you've loaded up a bunch of raw survey data into some flavor of spreadsheet app.</em></p>\n<p><strong>Step 1: Set Up Column Focus</strong></p>\n<ul>\n<li>Decide which freeform response you are going to categorize, and hide every column except for the question and an identifier column (UUID, email address, or generate your own).</li>\n<li>Sort the responses in reverse alphabetical (<code>Z-A</code>, yeah) order. Now you don't have to scroll past a bunch of empty cells to get to words.</li>\n</ul>\n<p><strong>Step 2: Start a New Blank Spreadsheet Workbook Thing</strong>\nThis new sheet will be where the categorization happens. I tend to throw everything into a single sheet, and then add additional sheets as needed (the tabs on the bottom when looking at Google Drive).</p>\n<p>The first row will contain headers for each of the general themes that will emrge. Each header should have two columns between them, because the data rows will have some text and the identifier.</p>\n<p><strong>Step 3: Initial Reading &#x26; Classification</strong>\nMove down, row-by-row, and read the responses. Find the theme(s) of the text, and record some or all of the text under the appropriate column, along with the unique id so that you can find the response later.</p>\n<p>Here's an example:</p>\n<p><em>The pizza was great! I love all the unique topping choices, and the music was rad. The ponly thing is that the kid working the register was a little rude.</em></p>\n<pre><code>| Toppings        |    |   | Music               |    |   | Staff |    |\n|-----------------|----|---|---------------------|----|---|-------|----|\n| Loved selection | id |   | \"the music was rad\" | id |   | Rude  | id |\n|                 |    |   |                     |    |   |       |    |\n</code></pre>\n<p><strong>Step 4: Summarization</strong>\nAfter breaking responses into groups, it becomes easier to quantify the qualitative data. You could split each theme into positive &#x26; negative, or take it further and group respondents in one theme by answers to another question to find other insights.</p>","id":"/Users/taylor/Documents/Projects/taylorlistens/src/pages/2017-12-19-affinity-method/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-12-19T19:49:09.695Z","path":"/affinity-method","title":"My Affinity Chart Method","excerpt":null,"tags":["research"]}}],"tagName":"research"}}

